@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
body {
  margin: 0;

  font-family:'Poppins', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

*{
  margin : 0px;
  padding : 0px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  padding: 40px 24px;
}

.app__searchForm {
  margin-top: 18px;
}

.app__input {
  padding: 6px 16px;
  min-width: 200px;
  max-width: 100vw;
  font-size: 15px;
}

.app__submit{
  padding: 6px 16px;
  font-size: 15px;
  margin-left: 6px;
  margin-right: 6px;
  background-color: rgb(82, 82, 212);
  border : 2px solid rgb(81, 81, 194);
  border-radius: 4px;
  color: white;
  cursor: pointer;
}

.app__submit:hover{
  padding: 6px 16px;
  font-size: 15px;
  margin-left: 6px;
  margin-right: 6px;
  background-color: white;
  border : 2px solid rgb(81, 81, 194);
  border-radius: 4px;
  color:rgb(82, 82, 212);
  cursor: pointer;
}

.app__recipes{
  margin-top: 24px;
  display: grid;
  grid-template-columns: 200px 200px 200px;
  grid-gap: 40px;
  -webkit-align-items: baseline;
          align-items: baseline;
}

.app_healthLabels{
  padding : 7px 8px;
  border-radius: 4px;
  
}

.recipeTile__img{
    height: 200px;
    width: 200px;
    object-fit: cover;
    border-radius: 4px;

}

.recipeTile__name {
    max-width: 200px;
    text-align: center;
    margin: 12px 0px;
}



