.App{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 40px 24px;
}

.app__searchForm {
  margin-top: 18px;
}

.app__input {
  padding: 6px 16px;
  min-width: 200px;
  max-width: 100vw;
  font-size: 15px;
}

.app__submit{
  padding: 6px 16px;
  font-size: 15px;
  margin-left: 6px;
  margin-right: 6px;
  background-color: rgb(82, 82, 212);
  border : 2px solid rgb(81, 81, 194);
  border-radius: 4px;
  color: white;
  cursor: pointer;
}

.app__submit:hover{
  padding: 6px 16px;
  font-size: 15px;
  margin-left: 6px;
  margin-right: 6px;
  background-color: white;
  border : 2px solid rgb(81, 81, 194);
  border-radius: 4px;
  color:rgb(82, 82, 212);
  cursor: pointer;
}

.app__recipes{
  margin-top: 24px;
  display: grid;
  grid-template-columns: 200px 200px 200px;
  grid-gap: 40px;
  align-items: baseline;
}

.app_healthLabels{
  padding : 7px 8px;
  border-radius: 4px;
  
}